import type { HttpWeblogInstance } from '@/types/index';

import { BasePlugin } from '../base';
import { autoTrackOption, visualMessageStatus, visualDataType, visualMessage, elementData, collectParam, MonitorShow } from './interface';
import { getXpathFromNode } from '../../util';
import { sendMessageToOpener, sendMessageToParent } from './sendMessage';
import { insertVisualJs } from './insertVisualJs';
import 'intersection-observer';

const defaultCollectAttribute = [
    'class',
    'alt',
    'src',
    'href'
]

// const defaultCollectTags = ['A', 'INPUT', 'BUTTON', 'TEXTAREA', 'DIV', 'UL', 'LI', 'SPAN'];
const defaultCollectTags: string[] = [];


const getTextContent = (element: HTMLElement | Node) => {
    let text = element.textContent;
    if (!text) return;
    text = text.trim();
    // 文本处理
    let finnalInnterText = text.substring(0, 20).trim();
    if (text.length > finnalInnterText.length) {
        finnalInnterText += '...';
    }
    return finnalInnterText;
};

export default class AutoTrack extends BasePlugin {
    declare weblog: HttpWeblogInstance;
    static override key = 'autoTrack';
    mode: 'all' | 'symbol' = 'all'; // 监听模式
    maxScrollX: number = 0; // X最大滚动距离
    maxScrollY: number = 0; // Y最大滚动距离
    scrollStayTime: number = 4000; // 驻足时长
    symbol: string = 'weblog-';
    tags: string[] = defaultCollectTags;
    attributes: string[] = defaultCollectAttribute;
    hiddenContent: boolean = false; // 是否上报innerText
    intersectionObserver?: IntersectionObserver;
    mutationObserver?: MutationObserver;
    monitorShow?: MonitorShow; // 是否监听元素曝光
    exactMatchElement?: (target: HTMLElement) => boolean;
    beforeSend?: (type: string, action: string, params: any) => boolean;
    followCurPage?: boolean = false; // 是否跟随当前页面page上报
    constructor(trackOption: autoTrackOption = {}) {
        super();
        if (typeof trackOption === 'object') {
            Object.assign(this, trackOption);
            if (this.tags && this.tags.length) {
                this.tags = this.tags.map(tag => tag.toLowerCase());
            }
            if (this.monitorShow) {
                if (typeof this.monitorShow !== 'object') this.monitorShow = {};
                if (!this.monitorShow.root || this.monitorShow.root.nodeType !== Node.ELEMENT_NODE) {
                    this.monitorShow.root = undefined;
                }
            }
        }
    }

    private isInsertToolbar: boolean = false; // 是否已插入工具栏
    private timeId: any = null; // 计时器ID
    private touchStartObject: {[key: number]: any} = {} ; // 多个触点touchStart的数据
    private touchMoveObject: {[key: number]: any} = {}; // 多个触点touchMove的数据

    override apply(weblog: HttpWeblogInstance) {
        this.weblog = weblog;
        this.init();
        // 进入页面先触发一次滚动上报
        this.scrollEvent();
    }

    init() {
        // Todo: delay内点了两次如何处理合理？1.立即上报第一次，第二次delay 2.取消第一次，第二次上报 3.两次都delay（目前的）
        // 放在cookie为了保障需要reload的页面
        const { getCookie } = this.weblog.Utils.cookie;
        const visiualSwitch = getCookie('_visual_switch');
        if (visiualSwitch === 'true') {
            !this.isInsertToolbar && insertVisualJs('toolbar');
            this.isInsertToolbar = true;
            return;
        }

        // 开始询问埋点平台是否注册圈选
        this.startRegisterAutoTrackPlugin();

        // 注册完成后告知parent
        this.applyPluginMessageToParent();

        // 绑定事件
        window.addEventListener('click', this.clickEvent, true);
        window.addEventListener('scroll', this.scrollEvent);
        window.addEventListener("message", this.messageEvent, false);
        if (this.isMobile()) {
            window.addEventListener('touchstart', this.touchStartEvent, true);
            window.addEventListener('touchmove', this.touchMoveEvent, true);
            window.addEventListener('touchend', this.touchEndEvent, true);
        }
        if (this.monitorShow) {
            // @ts-ignore
            this.weblog.on('event', this.resetObserver);
            // 如果有 autoPV
            if (this.weblog.plugins.autoPV) {
                this.initIntersecionObserver(true);
                this.initMutationObserver();
            }
        }
    }
    // PV 更新之后重置
    resetObserver = (event: any) => {
        const { type, action } = event;
        if (type === 'PV' && action === 'enter') {
            this.initIntersecionObserver(true);
            this.initMutationObserver();
        }
    }
    // 监听视图滚动曝光
    initIntersecionObserver(auto?: boolean) {
        this.intersectionObserver?.disconnect();
        const { root = null, rootMargin = '0px', threshold = 0.3 } = this.monitorShow!;
        this.intersectionObserver = new IntersectionObserver((entries: IntersectionObserverEntry[]) => {
            entries.forEach(entry => {
                if (entry.isIntersecting) {
                    this.collectElementShow(entry.target as HTMLElement, false, 'intersection');
                    this.intersectionObserver!.unobserve(entry.target);
                }
            });
        }, {
            root, rootMargin, threshold
        });
        if (auto) this.autoObserve();
    }
    autoObserve() {
        const { mutationDelay = 300 } = this.monitorShow!;
        setTimeout(() => {
            // 自动监听所有带标记的元素
            const elements = document.querySelectorAll(`[${this.symbol}show]`);
            if (elements.length) {
                elements.forEach(el => {
                    this.intersectionObserver?.unobserve(el);
                    this.intersectionObserver?.observe(el);
                });
            }
        }, Math.max(mutationDelay, 0));
    }
    // 主动监控元素滚动
    observe(element: Element) {
        if (!this.intersectionObserver) {
            this.initIntersecionObserver();
        }
        this.intersectionObserver?.observe(element);
    }
    // 监听元素插入曝光
    initMutationObserver() {
        try {
            if (typeof window.MutationObserver === 'undefined') return;
            this.mutationObserver?.disconnect();
            const { root = document.body, mutationDelay = 300 } = this.monitorShow!;
            this.mutationObserver = new window.MutationObserver((mutations) => {
                setTimeout(() => {
                    const mutation = mutations.pop();
                    const addedNodes = mutation && mutation.addedNodes;
                    if (addedNodes && addedNodes.length) {
                        for(let i = 0; i < addedNodes.length; i ++) {
                            const el = addedNodes[i] as HTMLElement;
                            if (el && el.getBoundingClientRect) {
                                const { x, y, width, height } = el.getBoundingClientRect();
                                // 如果元素插入时不在可视区，则添加到 intersectionObserver 中
                                if (Math.max(width, height) === 0 || y > window.innerHeight || x > window.innerWidth) {
                                    this.observe(el);
                                } else {
                                    this.collectElementShow(el, true, 'mutation');
                                }
                            }
                        }
                    }
                });
            });
            setTimeout(() => {
                this.mutationObserver && this.mutationObserver.observe(root, { childList: true, subtree: true });
            }, Math.max(mutationDelay, 0));
        } catch (err) {
            console.error(err);
        }
    }
    // 元素曝光事件
    collectElementShow(element: HTMLElement, subtree?: boolean, trackMethod?: string) {
        if (element.nodeType !== Node.ELEMENT_NODE) return;
        const attr = `${this.symbol}show`;
        let signedEls = [element];
        if (subtree) {
            // 对于子元素，只对有标记的元素曝光
            signedEls = signedEls.concat(Array.from(element.querySelectorAll(`[${attr}]`)));
        }
        signedEls.forEach(el => {
            const xpath = getXpathFromNode(el as HTMLElement);
            this.trackCodeEvent('show', el as HTMLElement, { xpath, trackMethod });
            if (el === element && this.mode === 'all') {
                const { nodeName } = el;
                const innerText = getTextContent(el);
                const attributesSerialize = this.pickElementAttrs(el as HTMLElement);
                this.collect('SHOW', {
                    xpath,
                    innerText,
                    nodeName,
                    attributesSerialize,
                    trackMethod
                }, 'WEBLOGGER_AUTO_TRACK_SHOW');
            }
        });
    }

    // touchStart
    private touchStartEvent = (event: TouchEvent) => {
        const touches = event.touches;
        for (let i = 0; i < touches.length; i++) {
            const { clientX, clientY, identifier } = touches[i]!;
            if(this.touchStartObject[identifier]) {
                return;
            }
            this.touchStartObject[identifier] = {
                clientX: Number(clientX.toFixed(2)),
                clientY: Number(clientY.toFixed(2)),
                timestamp: Date.now()
            };
        }
    }

    // touchMove
    private touchMoveEvent = (event: TouchEvent) => {
        const touches = event.changedTouches;
        for (let i = 0; i < touches.length; i++) {
            const { clientX, clientY, identifier } = touches[i]!;
            const data = {
                clientX: Number(clientX.toFixed(2)),
                clientY: Number(clientY.toFixed(2)),
                timestamp: Date.now()
            };
            if(this.touchMoveObject[identifier]) {
                this.touchMoveObject[identifier].push(data);
                return;
            }
            this.touchMoveObject[identifier] = [data];
        }
    }

    // touchEnd
    private touchEndEvent = async(event: TouchEvent) => {
        const touches = event.changedTouches;
        const touchesInfo = [];
        for (let i = 0; i < touches.length; i++) {
            const { clientX, clientY, identifier }  = touches[i]!;
            const touchItem = {
                touchStart: {},
                touchMove: [],
                touchEnd: {}
            }
            const currentTouchStartPointerInfo = this.touchStartObject[identifier];
            const currentTouchMovePointerInfo = this.touchMoveObject[identifier];
            // touchstart info
            if(currentTouchStartPointerInfo) {
                touchItem.touchStart = currentTouchStartPointerInfo;
                delete this.touchStartObject[identifier];
            }
            // touchmove info
            if(currentTouchMovePointerInfo) {
                touchItem.touchMove = currentTouchMovePointerInfo;
                delete this.touchMoveObject[identifier];
            }
            // touchend info
            touchItem.touchEnd = {
                clientX: Number(clientX.toFixed(2)),
                clientY: Number(clientY.toFixed(2)),
                timestamp: Date.now()
            }
            touchesInfo.push(touchItem);
        }
        const formatData = this.dataFormat(event);
        if (formatData) {
            this.collect('CLICK', formatData , 'WEBLOGGER_AUTO_TRACK_TOUCH');
        }
    }

    private clickEvent = async(event: MouseEvent | TouchEvent) => {
        const formatData = this.dataFormat(event);
        if (formatData) {
            this.collect('CLICK', formatData, 'WEBLOGGER_AUTO_TRACK_CLICK');
        }
    }

    private scrollEvent = ():void => {
        // 停止scrollStayTime时间才算做停留过
        // 与晓康分歧：是不是应该将每个滚动先做计算再进行延时
        this.timeId && clearTimeout(this.timeId);
        this.timeId = setTimeout(() => {
            const { innerWidth, innerHeight, scrollX, scrollY } = window;
            const bodyClientWidth = document.body.clientWidth;
            const currentScrollX = Math.min(innerWidth + scrollX, bodyClientWidth);
            const currentScrollY = innerHeight + scrollY;
            if(currentScrollX > this.maxScrollX || currentScrollY > this.maxScrollY) {
                this.maxScrollX = currentScrollX;
                this.maxScrollY = currentScrollY;
                this.timeId = null;
            }
        }, this.scrollStayTime);
    }

    private messageEvent = (event: MessageEvent):void => {
        const { data } = event;
        const { setCookie } = this.weblog.Utils.cookie;
        // 工具栏
        if (data && data.type === 'visual') {
            const messageData: visualMessage = data.data;
            const { status, visualData } = messageData;
            if (status === visualMessageStatus.RECEIVED) {
                // 种cookie是否插入toolbar
                const expires = new Date(+Date.now() + 1000 * 60 * 60);
                setCookie('_visual_switch', 'true', {
                    expires
                });
                setCookie('_visual_username', visualData.userName, {
                    expires
                });
                setCookie('_visual_appName', visualData.appName, {
                    expires
                });
                // 会收到uuid生成的id，放到sessionStorage里
                try {
                    sessionStorage.setItem('_weblog_visual_data', JSON.stringify({ id: visualData.id }));
                } catch (err) {}
                // 插入toolbar
                !this.isInsertToolbar && insertVisualJs('toolbar');
                this.isInsertToolbar = true;
            }
        }
        // 热力图&触发率图
        if (data && data.type === 'weblogger_auto_track_hot_chart') {
            insertVisualJs('hotChart');
        }
    }

    private collect = async (type: string, params: collectParam, action: string) => {
        if(this.hiddenContent) params.innerText = undefined;
        if (typeof this.beforeSend === 'function') {
            try {
                await this.beforeSend(type, action, params);
            } catch (err) {
                console.error(err);
            }
        }
        this.weblog.collect(
            // @ts-ignore
            type.toUpperCase(),
            {
                action,
                params,
                urlPage: this.followCurPage ? this.weblog.currentUrlPackage.page : this.weblog.currentUrlPackage.getRealUrlPackage()
            }
        );
    }

    // 是不是移动端
    private isMobile(): boolean {
        const ua = navigator.userAgent;
        const isAndroid = ua.indexOf('Android') > -1 || ua.indexOf('Adr') > -1; //android终端
        const isiOS = !!ua.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
        return isAndroid || isiOS;
    }
    // 元素过滤
    elementFilter(target: HTMLElement, type: string) {
        if (!target) return false;
        if (this.mode === 'symbol' && !target.hasAttribute(`${this.symbol}type`)) return;
        const { tagName } = target;
        // 顶级标签不采集
        if (['html', 'body'].indexOf(tagName.toLowerCase()) !== -1) return false;

        // 是否符合用户筛选的tag
        if (this.tags && this.tags.length && this.tags.indexOf(tagName) === -1) return false;

        // 是否满足用户精确匹配,未匹配则不继续执行
        if (this.exactMatchElement && target && !this.exactMatchElement(target)) return false;
        return true;
    }
    // 属性采集
    pickElementAttrs(target: HTMLElement) {
        const { attributes } = target;
        const params: {[key: string]: string} = {};
        for(let i = 0; i < attributes.length; i++) {
            const { name, value } = attributes[i]!;
            if (name.indexOf(this.symbol) === 0) continue;
            // 强制采集：自定义属性
            if (name.indexOf('data-') === 0) {
                params[name] = value;
            } else if (this.attributes && this.attributes.indexOf(name) !== -1) {
                params[name] = value;
            }
        }
        return params;
    }
    // 代码埋点触发
    trackCodeEvent(type: string, target: HTMLElement, extraParams?: {[key: string]: any}) {
        const weblogTag = this.symbol + type;
        if (target.hasAttribute(weblogTag)) {
            const value = target.getAttribute(weblogTag);
            if (value) {
                try {
                    const { action, params } = JSON.parse(value);
                    if (action) {
                        this.collect(type, { ...params, ...extraParams }, action);
                    }
                } catch (err) {
                    console.error(`${weblogTag} parsed error:`, err);
                }
            }
        }
    }

    // 数据组织
    dataFormat(event: (MouseEvent | TouchEvent)): elementData | undefined {
        const { target, clientX, clientY, type } = event as (MouseEvent | TouchEvent) & { target: HTMLElement; clientX: number; clientY: number;};
        const enable = this.elementFilter(target, type);
        if (!enable) return;
        const bounding = target.getBoundingClientRect();
        const x = clientX - bounding.x;
        const y = clientY - bounding.y;
        const xpath = getXpathFromNode(target);
        const { nodeName } = target;
        const innerText = getTextContent(target);
        const attributesSerialize = this.pickElementAttrs(target);
        this.trackCodeEvent(type, target, { xpath });
        return { xpath, innerText, nodeName, attributesSerialize, clientX, clientY, x, y }
    }

    private startRegisterAutoTrackPlugin() {
        if(window.opener) {
            // 被新窗口打开
            // 已经开始注册plugin
            const weblogVisualData = sessionStorage.getItem('_weblog_visual_data');
            const formatData: visualDataType | undefined = weblogVisualData && JSON.parse(weblogVisualData);
            const id = formatData ? formatData.id : 0; // 第一次发为0，就给新id
            sendMessageToOpener({
                data: {
                    id,
                    status: visualMessageStatus.START
                },
                type: 'visual'
            });
        }
    }

    private applyPluginMessageToParent() {
        sendMessageToParent({
            data: {},
            type: 'weblogger_auto_track_finish'
        });
    }

    override beforeUnload = () => {
        this.weblog.sendImmediately('PULL_DOWN', {
            action: 'WEBLOGGER_AUTO_TRACK_SCROLL',
            params: {
                maxScrollX: this.maxScrollX,
                maxScrollY: this.maxScrollY
            }
        });
    }

    destroy() {
        window.removeEventListener('click', this.clickEvent, true);
        window.removeEventListener('scroll', this.scrollEvent);
        window.removeEventListener("message", this.messageEvent, false); 
        if (this.isMobile()) {
            window.removeEventListener('touchstart', this.touchStartEvent, true);
            window.removeEventListener('touchmove', this.touchMoveEvent, true);
            window.removeEventListener('touchend', this.touchEndEvent, true);
        }
    }
}