import { sendMessageToParent } from './sendMessage';

export function insertVisualJs(type: string) {
    const head = document.getElementsByTagName('head')[0];

    // js
    // const timeStamp = String(+Date.now()).slice(0, -7); // 过期时间2个多小时
    const timeStamp = String(+Date.now()); // 每次拉新的
    const script = document.createElement('script');
    script.crossOrigin = 'anonymous';
    script.type = 'text/javascript';
    script.src = insertUrlHandle(type, timeStamp);

    script.onload = () => {
        const visualTrack = new (window as any).VisualTrack();
        visualTrack.init();
        if (type === 'hotChart') {
            sendMessageToParent({
                data: {},
                type: 'weblogger_auto_track_hot_chart_finish'
            });
        }
    }

    head?.appendChild(script);
}

function insertUrlHandle(type: string, timeStamp: string) {
    let result = '';
    switch(type) {
        case 'toolbar':
            result = `https://static.yximgs.com/udata/pkg/ks-track-platform-new/visual-track-plugin/index.js?${timeStamp}`;
            break;
        case 'hotChart':
            result = `https://static.yximgs.com/udata/pkg/ks-track-platform-new/visual-track-plugin/vice.js?${timeStamp}`;
            break;
        default:
            break;
    }
    return result;
}